// pages/tenant/chat/[ownerId].tsx
"use client";
import { useEffect, useState } from "react";
import { HubConnectionBuilder } from "@microsoft/signalr";

export default function TenantChat({ ownerId }: { ownerId: string }) {
  const [connection, setConnection] = useState<
    import("@microsoft/signalr").HubConnection | null
  >(null);
  const [messages, setMessages] = useState<
    { senderId: string; message: string }[]
  >([]);
  const [message, setMessage] = useState("");

  useEffect(() => {
    const newConnection = new HubConnectionBuilder()
      .withUrl("https://your-api/chatHub")
      .withAutomaticReconnect()
      .build();

    setConnection(newConnection);
  }, []);

  useEffect(() => {
    if (connection) {
      connection.start().then(() => {
        connection.on("ReceiveMessage", (senderId, message) => {
          setMessages((prev) => [...prev, { senderId, message }]);
        });
      });
    }
  }, [connection]);

  const sendMessage = async () => {
    if (connection) {
      await connection.invoke("SendMessage", "tenantId", ownerId, message);
      setMessages([...messages, { senderId: "tenantId", message }]);
      setMessage("");
    }
  };

  return (
    <div className="chat">
      {messages.map((msg, index) => (
        <div key={index}>
          {msg.senderId}: {msg.message}
        </div>
      ))}
      <input value={message} onChange={(e) => setMessage(e.target.value)} />
      <button onClick={sendMessage}>Gá»­i</button>
    </div>
  );
}
